<template>
  <div class="app">
    <div class="list" v-if="dam == '登录/注册'">
      <div class="img">
        <img
          src="https://s.qunarzz.com/ordercenter_mobile/images/order_login.png"
          alt=""
        />
      </div>
      <p>您还没有本地订单</p>
      <p>快登陆查看在线订单吧</p>
      <button @click="gologin">登录</button>
    </div>
    <div class="it" v-if="arr.length > 0">
      <div class="box" v-for="(item, index) in arr" :key="index">
        <div class="img">
          <img :src="item.img" alt="" />
        </div>
        <div class="content">
          <div class="title">{{ item.title }}</div>
          <div class="bottom">
            <div class="price">￥{{ item.price }}</div>
            <div class="delete" @click="remove(index)">
              <img src="@/assets/images/shanchu.png" alt="" />
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else class="item">
      您还没有决定去哪儿旅行哦！
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dam: '',
      arr: [],
    };
  },
  created() {
    if (JSON.parse(localStorage.getItem("Item")) !=undefined && JSON.parse(localStorage.getItem("Item")) !=null) {
      this.arr = JSON.parse(localStorage.getItem("Item"));
    } else {
      this.arr = [];
    }
    if (JSON.parse(localStorage.getItem("list"))) {
      this.dam = JSON.parse(localStorage.getItem("list")).username;
    } else {
      this.dam = "登录/注册";
    }
    console.log(this.dam);
    console.log(this.arr);
  },
  watch: {
    $route(to) {
      if (to.path == "/list") {
        console.log(JSON.parse(localStorage.getItem("Item")));
        this.arr = JSON.parse(localStorage.getItem("Item"));
        if (JSON.parse(localStorage.getItem("list"))) {
          this.dam = JSON.parse(localStorage.getItem("list")).username;
          console.log(this.dam);
        } else {
          this.dam = "登录/注册";
          console.log(this.dam);
        }
      }
    },
  },
  methods: {
    gologin() {
      this.$router.push({ path: "/login" });
    },
    remove(index) {
      console.log(index);
      this.arr.splice(index, 1);
      console.log(this.arr);
      localStorage.setItem("Item", JSON.stringify(this.arr));
    },
  },
};
</script>

<style scoped>
.it {
  margin-bottom: 1.333333rem;
}
.list {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #efefef;
  text-align: center;
  font-size: 0.533333rem;
}
.list > button {
  width: 2.666667rem;
  height: 1.066667rem;
  color: #fff;
  background-color: orange;
  border: none;
  margin: 0.266667rem;
  font-size: 0.533333rem;
}
.item {
  width: 100%;
  text-align: center;
  font-size: 0.4rem;
  margin-top: 1.333333rem;
}
.box {
  display: flex;
  justify-content: center;
  margin: 0.266667rem;
}
.box > .img {
  width: 2.666667rem;
  height: 2.666667rem;
}
.box > .img img {
  width: 100%;
  height: 100%;
}
.box > .content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 2.666667rem;
  margin-left: 0.266667rem;
}
.box > .content > .title {
  font-size: 0.4rem;
}
.box > .content > .bottom {
  display: flex;
  justify-content: space-between;
}
.box > .content > .bottom > .price {
  color: orange;
  font-size: 0.333333rem;
}
</style>
